/* eslint-disable no-console */
import {
	precacheAndRoute,
	cleanupOutdatedCaches
} from 'workbox-precaching';

// 缓存资源
precacheAndRoute(self.__WB_MANIFEST || []);
cleanupOutdatedCaches();

self.skipWaiting();
self.clientsClaim();

// 推送通知
self.addEventListener('push', event => {
	let data = {};
	if (event.data) {
		try {
			data = event.data.json();
		} catch {
			data = {
				message: event.data.text()
			};
		}
	}

	const title = data.title || '你有一条新消息';
	const options = {
		body: data.message || '点击查看详情',
		icon: data.icon || '/192x192.png',
		badge: data.badge || '/192x192.png',
		data: {
			url: data.url || '/',
			id: data.id || null,
		},
	};

	event.waitUntil(
		self.registration.showNotification(title, options)
	);
});

// 通知点击
self.addEventListener('notificationclick', event => {
	event.notification.close();
	const urlToOpen = event.notification.data.url;
	const eventId = event.notification.data.id;

	// 上报点击
	if (eventId) {
		const trackClick = new Request('/api/webpush/click', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({
				id: eventId
			}),
		});
		fetch(trackClick).catch(() => {});
	}

	event.waitUntil(
		self.clients.matchAll({
			type: 'window',
			includeUncontrolled: true
		})
		.then(windowClients => {
			for (const client of windowClients) {
				if (client.url === urlToOpen && 'focus' in client) return client.focus();
			}
			if (self.clients.openWindow) return self.clients.openWindow(urlToOpen);
		})
	);
});